body{
    font-family: 'Varela', sans-serif;
    margin:0;
    background-color:rgb(238, 232, 253) ;
}
.ad-container{
    border: 1px solid black;
    padding:1.5em;
    margin:10px;
}
h4
{
    text-align: center;
    margin:0;
    margin-bottom: 0.5em;
    font-size: 1.4em;
    border-bottom:1px solid black;
}
h1{
    margin:0;
    margin-bottom: 1em;
}
.container
{
    max-width: 500px;
    background-color:rgb(238, 232, 253); 
    margin:auto;
    height: 100%; 
    padding:10px;
}
.status-container
{
display: grid;
grid-template-columns: 1fr 1fr;
text-align: center;
align-self: center;
padding:10px;
margin:5px 20px;
background-color:white;
font-size: 1.6em;
box-shadow: 2px 6px 5px -4px rgba(0,0,0,0.75);
margin-bottom: 1em;
}

.income div:last-child
{
    
    color:#61ee0d;
}
.income{
    border-right:1px solid black;
}
.expense div:last-child
{
    color: red;
}

/*css fro history*/
h5{
border-bottom:1px solid black;
padding-bottom: 5px;
font-size:20px;
margin:0px;
margin-bottom: 10px;
}
ul{
    display: grid;
    grid-template-rows: 1fr;
    margin:0px;
    padding:0px;
    font-size: 1.4em;
    margin-bottom: 1em;
}
li{
    list-style: none;
    padding:2px;
    border:1px solid black;
    margin:5px;
    display: flex;
    justify-content: space-between;
    border-right: 5px solid rgb(0, 255, 0);
    
}
li:hover
{
    transition-delay: 1s;
    position: relative;
    right: -10px;
}
input{
    outline:none;
    margin:5px;
    background-color: white;
    border:none;
    font-size: 2em;
    border:1px solid black;
    

}
.transcation-grid
{
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.btn
{
    margin:1em;
    padding:10px;
    outline:none;
    border:2px solid black;
    box-shadow: 2px 6px 5px -4px rgba(0,0,0,0.75);
    font-size: 1.5em;

}

.btn:hover{
    background-color: yellowgreen;
}

.btn-add
{
    padding:10px;
    background-color: violet;
    margin:5px;
    outline:none;
    border:1px solid black;
    font-size: 1.5em;

}
.add-container
{
    display: flex;
    flex-direction: column;
}

@media(min-width:600px)
{
    body{
    max-width: 350px;
    margin:auto;
    font-size:10px;
    }
    .status-container
    {
        margin-bottom: 10px;
    }
    input{
        font-size: 20px;
    }
    ul{
        margin-bottom: 10px;
    }
    h1{
        margin-bottom: 10px;
    }
}